















import { Component, Vue, Watch } from 'vue-property-decorator';
declare var AuthBar: any;

@Component({
  components: {
  },
})
export default class Splash extends Vue {
  get environment() {
    return process.env.VUE_APP_ENVIRONMENT;
  }

  get authToken() {
    return this.$store.state.auth.authToken;
  }

  @Watch('authToken')
  private onPropertyChanged(value: string, oldValue: string) {
    if (value && !oldValue) {
      this.$router.push('/');
    }
  }

  private signIn() {
    AuthBar.showSignIn();
  }
}
